// provide/inject
// this.$parentH5H5this.$parent.$parent.xxx
export default function getParent(name, keys) {
	let parent = this.$parent;
	// whileH5
	while (parent) {
		// 
		if (parent.$options.name !== name) {
			// name
			parent = parent.$parent;
		} else {
			let data = {};
			// 
			for(let i in keys) {
				// 
				data[i] = parent[i] ? parent[i] : keys[i];
			}
			return data;
		}
	}

	return {};
}