// //  post 
function _post(url, data, success, fail, complete) {
	// 
	data = data || {}
	//post
	uni.request({
		url: api_root + url, //
		header: {
			'Content-Type': 'application/json',
			'X-Xsh-Token': wx.getStorageSync('token') || '',
		},
		method: 'POST',
		data: data,
		success: res => {
			if (res.statusCode !== 200) {
				uni.showModal({
					title: '',
					content: '',
					success: function(res) {
					
					}
				})
				fail && fail();
				return false;
			}

			// 
			if (res.data.errno == 501) {
				uni.navigateTo({
					url: "/pages/login/login"
				})
				return false
			}

			// 
			if (res.data.errno !== 0) {
				uni.showModal({
					title: '',
					content: res.data.errmsg,
					success: function(res) {
						fail && fail(res.data);
					}
				})
				return false;
			}
			success && success(res.data)
		},
		fail: res => {
			uni.showModal({
				title: '',
				content: '',
				success: function(res) {
					uni.navigateBack({
						delta: 1
					})
				}
			})

			fail && fail(res.data);
		},
		complete: function(res) {
			uni.hideLoading()
			complete && complete(res);
		}
	});
}
